$(window).scroll(function() {
    if ($(window).scrollTop() > 100) {
        $(".scroll-up").addClass("_active")
    } else {
        $(".scroll-up").removeClass("_active")
    }
})

$(".scroll-up").click(function() {
    $(window).scrollTop(0);
})


const textareas = document.querySelectorAll("textarea");
textareas.forEach(textarea=>{
    textarea.addEventListener("input", e =>{
        textarea.style.height = "63px";
        let scHeight = e.target.scrollHeight;
        textarea.style.height = `${scHeight}px`;
    });
})

$(".comment-field textarea").on('input',function () {
    let val = $(this).val()
    if(val !== ""){
        $(this).parents(".comment-field").addClass("_show")
    } else{
        $(this).parents(".comment-field").removeClass("_show")
    }
})

$("._tab").click(function() {
    let parentBlock = $(this).parents(".tabs-parent")
    let tabId = $(this).attr("data-tab")
    console.log(parentBlock)
    console.log(tabId)
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find("._tab-content").removeClass("_active")
    $(parentBlock).find(`._tab-${tabId}`).addClass("_active")
})

$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})
