
if(window.innerWidth>992){
    $(".auto-letter__toggle").mouseover(function () {
        $(this).parents(".auto-letter").siblings(".auto-letter").removeClass("_active")
        $(this).parents(".auto-letter").addClass("_active")
    })


    $(".auto-list").mouseleave(function () {
        $(".auto-letter").removeClass("_active")
    })
}else{
    $(".auto-letter__toggle").click(function () {
        $(this).parents(".auto-letter").siblings(".auto-letter").removeClass("_active")
        $(this).parents(".auto-letter").addClass("_active")
    })
    $(".auto-letter__dropdown").click(function () {
        $(".auto-letter").removeClass("_active")
    })
}
