$(".nav__item").hover(onIn, onOut);
$(".nav__item-show").click(function() {
    if ((window.innerWidth < 993)) {
        $(this).parents(".nav__item").toggleClass("_active")
        $(this).siblings(".nav__item-hidden").slideToggle()
    }
})

function onIn() {
    if (window.innerWidth > 993) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                el.addClass("_active")
            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 993) {
        $(this).removeClass("_active")
    }
}

$("._show-search").click(function () {
    $(".header-search").addClass("_active")
})


$(document).mouseup(function(e) {
    var div = $('.header-search');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).addClass("_active")){
            $(div).removeClass("_active")
        }
    }
});

$("._show-menu").click(function () {
    $(".header").addClass("_open")
    $("body").addClass("fixed-body")
})

$(".menu__close").click(function () {
    $(".header").removeClass("_open")
    $("body").removeClass("fixed-body")
})
