$(".article-content__btn").click(function () {
    let parent = $(this).parents(".article-content")
    $(parent).toggleClass("_hide")
    $(parent).find(".article-content__bottom").slideToggle()
})


$(".quiz__radios .m-radio input").change(function () {
    $(this).parents(".quiz").addClass("_voted")
})


function setNum(el,count){
    $(el).html(count)
}



function likes(el,siblings){
    let numEl = $(el).find(".reactions__btn-text")
    let num = +$(numEl).html().trim()
    if($(el).hasClass("_active")){
        $(el).removeClass("_active")
        if(num===0){
            setNum(numEl,0)
        } else{
            setNum(numEl,num-1)
        }
    } else{
        $(el).addClass("_active")
        setNum(numEl,(num+1))
    }

    let unlikeEl = $(el).siblings(`.${siblings}`).find(".reactions__btn-text")
    let unlikeNum = +$(unlikeEl).html().trim()

    if($(el).siblings(`.${siblings}`).hasClass("_active")){
        if(unlikeNum<1){
            setNum(unlikeEl,0)
        } else{
            setNum(unlikeEl,(unlikeNum-1))
        }
    }
    $(el).siblings(`.${siblings}`).removeClass("_active")
}

$("._like").click(function () {
    likes($(this),"_unlike")
})

$("._unlike").click(function () {
    likes($(this),"_like")
})

$("._favourite").click(function () {
    let numEl = $(this).find(".text18")
    let num = +$(numEl).html().trim()
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        if(num===0){
            setNum(numEl,0)
        } else{
            setNum(numEl,num-1)
        }
    } else{
        $(this).addClass("_active")
        setNum(numEl,(num+1))
    }
})

$(".comment-card__toggle").click(function () {
    let parent = $(this).parents(".comment-card")
    $(parent).toggleClass("_expanded")
    $(parent).next(".comment-subgroup").slideToggle()
})


$("._show-answer").click(function () {
    $(this).parents(".comment-card").children(".comment-card__content").children(".comment-field").slideToggle()
})
