function autoDealerSlider() {
    var swiper = new Swiper('.auto-dealers .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        spaceBetween: 2,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 0,
        slidesPerView: "auto",
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: '.gallery .swiper-button-next',
            prevEl: '.gallery .swiper-button-prev',
        },
    });
}

let articleSlidersArray = []

function articleSliders() {
    $(".article-slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        articleSlidersArray[index] = new Swiper(`.article-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 10,
            effect: 'fade',
            loop: true,
            fadeEffect: {
                crossFade: true
            },
            navigation: {
                nextEl: `.article-slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.article-slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.article-slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'fraction',
                clickable: true,
            },
        })
    })
}

function aboutSlider() {
    var swiper = new Swiper('.about-slider .swiper-container', {
        slidesPerView: 'auto',
        spaceBetween: 0,
        pagination: {
            el: '.about-slider .swiper-pagination',
            type: 'bullets',
            clickable: true,
            dynamicBullets: true,
        },
    })
}


autoDealerSlider()
gallery()
articleSliders()
aboutSlider()
